.page-booking{
	min-height:450px;
	padding-bottom:50px;
}

.house-title{
	height: 44px;
	padding:0 10px;
	font-size: 16px;
	display: block;
	border-bottom:1px solid #E9E9E9;
}

.house-title-text{
	display: inline-block;
	width: 40%;
	height: 40px;
	line-height: 46px;
	color:#1E1E1E;
	vertical-align: middle;
}
.house-title-choose,
.house-title-hasbed
{
	display: inline-block;
	width: 40%;
	height: 40px;
	line-height: 46px;
	color:#0977C5;
	float:right;
	text-align:right;
	margin-right:5px;
	vertical-align: middle;
}

.house-list{
	display:flex;
	flex-wrap:wrap;
	align-content: flex-start;
	padding:15px 2%;
}

.house-list .mint-button{
	width: 20.5%;
	margin:5px 2%;
	background: #FFFFFF;
	border: 1px solid #D7D7D7;
	border-radius: 4px;
	box-shadow: none;
}
.house-list .primary{
	color:#fff;
	background: #0977C5;
	border: 1px solid #0977C5;
}


.house-title-hasbed{
	font-size:15px;
	color: #828282;
}
.circle-gray{
	display:inline-block;
	width:12px;
	height:12px;
	margin:0 3px;
	border-radius:50%;
	border:1px solid #0977C5;
}


.bed-list{
	min-height:200px;
	padding:15px;
}
.bed-list table {
	clear: both;
	width: 100%;
	border-collapse: collapse;
	color: #828282;
	font-size:15px;
}
.bed-list table thead{
	background: #F0F0F0;
	box-shadow: 0 -1px 0 0 #D7D7D7, 0 1px 0 0 #D7D7D7;
}

.bed-list table tr td:last-child{
	border-right:0;
}
.bed-list table thead tr{
	border-bottom:1px solid #D7D7D7
}
.bed-list table tbody tr{
	background:url(../../images/td_bg@3x.png) 0 0 repeat;
  	background-size: 10px 51px;
}
.bed-list table td {
	width: 33%;
	text-align: center;
	border-right:1px solid #D7D7D7;
}
.bed-list table thead td{
	height: 44px;
	
	vertical-align: middle;
}
.bed-list table tbody td{
	vertical-align: top;
	padding:0;
}

.bed-item{
	height: 51px;
    line-height: 51px;
}



.fix-order{
	display:flex;
}
.order-summary{
	flex:2;
	padding:4px 15px;
}

.order-price{
	display:block;
	clear:both;
	font-size: 16px;
	color: #1E1E1E;
	line-height: 20px;
}
.order-extra{
	font-size:12px;
	color: #828282;
}

.fix-order .mint-button{
	flex:1;
	background: #0977C5;
}

.mint-radio-input:checked + .mint-radio-core{
	border-color: #0977C5;
	background: #fff;
}
.mint-radio-input:checked + .mint-radio-core::after{
	width: 10px;
	height: 10px;
	left: 4px;
	top: 4px;
	background: #0977C5;
}


