.tab-city{
  display:flex;
  background:#fff;
  border-bottom: 1px solid #E9E9E9;
}
.tab-line{
  width:1px;
  height:20px;
  margin-top:10px;
  background:#E9E9E9;
}
.tab-city .mint-button {
  flex:1;
  font-size: 16px;
  border-radius:0;
  box-shadow: none;
  background:url(../../images/down_icon@3x.png) #fff 85% 14px no-repeat;
  background-size: 12px auto;
}
.tab-city .mint-button-text{
  display: block;
  width:80%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.tab-city .mint-button.active{
  color: #0977C5;
  background:url(../../images/up_icon@3x.png) #fff 85% 14px no-repeat;
  background-size: 12px auto;
}
.tab-city .v-modal{
  top: 86px;
}

.city-popup{
  width: 100%;
  height: 250px;
  padding: 0;
  transform: translate(-50%, 0);
  overflow:scroll;
}
.mint-popup ul{
  margin:10px 0;
  padding:0 20px;
} 
.mint-popup ul li{
  height:40px;
  line-height:40px;
  color:#828282;
}
.mint-popup ul .active{
  color: #0977C5;
  background:url(../../images/selected_icon@3x.png) right 10px no-repeat;
  background-size: 15px 15px;
}


