
.page-jobs{
  padding-bottom:50px;
}
.part-time-title{
  margin:0 15px;
  padding:15px 0;
  border-bottom: 1px dashed #D7D7D7;
}
.mint-swipe {
  width: 100%;
  height: 160px;
  overflow: hidden;
}
.mint-swipe-item img{
  width: 100%;
  height: 100%;
}
.fix-signup{
  display:flex;
}
.fix-signup .mint-button--normal{
  box-shadow:none;
  flex: 2;
  font-size:16px;
}
.fix-signup .mint-button--primary{
  background: #0977C5;
  box-shadow:none;
  flex: 3;
  font-size:16px;
}




