
.job-item{
  margin:10px 0;
  padding:15px;
  background: #FFFFFF;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
}
.job-title{
  margin:10px 0;
  height:16px;
  line-height:16px;
  font-size: 17px;
  color: #1E1E1E;
}
.job-info{
  display:flex;
}
.job-item .job-info li{
  margin-right:20px;
  font-size:15px;
  color:#828282;
}
.job-item .job-info li span{
  margin:0 2px;
  vertical-align: middle;
}
.job-salary{
  margin-top:10px;
  font-size:14px;
}
.job-salary .money{
  font-size:17px;
}




