
.home-header{
  position: absolute;
  left:0;
  top:0;
  width:100%;
  background: none;
}
.home-header .mint-button-text{
  font-size:14px;
}
.user-info{
  display:flex;
  flex-direction: column;
  height:200px;
  margin:0;
  color:#fff;
  text-align:center;
  background:url(../../../images/mine_bg@3x.jpg) #fff 0 0 no-repeat;
  background-size: 100% 100%;
}
.user-info .avatar{
  width:60px;
  height:60px;
  border:2px solid #fff;
  border-radius:32px;
}
.mask{
  position:relative;
  width:64px;
  height:64px;
  margin:60px auto 12px;
}
.mask .upload{
  position:absolute;
  width:64px;
  height:64px;
  left:0;
  top:0;
  opacity:0;
}
.user-info p{
  line-height:24px;
}
.user-info p input{
  text-align: center;
  background: none;
  border: none;
  color: #fff;
  font-size:14px;
}
.user-info .name input{
  font-size:18px;
}
.timeline{
  background:#fff;
  border-bottom:1px solid #E9E9E9;
  overflow:hidden;
}
.timeline dt{
  height:36px;
  line-height:40px;
  padding-left:15px;
  font-size: 15px;
  color: #828282;
  background:#f0f0f0;
  border-bottom:1px solid #E9E9E9;
}
.timeline dd{
  position: relative;
  min-height: 50px;
  padding-top: 35px;
}
.item-time{
  position: absolute;
  left: 22px;
  top: 25px;
  width: 50px;
  font-size: 14px;
  line-height: 20px;
}
.item-time span{
  display: block;
  font-size: 20px;
  color: #1E1E1E;
}
.item-line{
  left: 70px;
  position: absolute;
  height: 100%;
  border: 1px solid #E9E9E9;
}
.item-icon{
  position: absolute;
  left:66px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #0977C5;
  width: 10px;
  height: 10px;
}
.item-content{
  position: relative;
  padding-bottom: 10px;
  left: 90px;
  top: -3px;
  width:70%
}



