
.mint-swipe {
  height: 170px;
  overflow: hidden;
}
.mint-swipe-item img{
  width: 100%;
}
.hotel-info{
  position: relative;
  margin-bottom: 10px;
  min-height:90px;
  padding: 10px 15px;
  background: #fff;
}
.hotel-info .hotel-tel {
  position: absolute;
  right: 10px;
  top: 20px;
  width: 60px;
  height: 70px;
  border-left: 1px solid #E9E9E9;
}
.hotel-info .icon-call{
  width: 60px;
  height: 70px;
  background: url(../../../images/call_icon@3x.png) 15px 20px no-repeat;
  background-size: 26px auto;
}
.check-info{
  margin-top: -11px;
}
.check-info h5{
  margin: 0;
  padding: 10px 15px;
  background: #f0f0f0;
  font-size: 15px;
  color: #828282;
  font-weight: 400;
}
.check-info .check-sex{
  display: flex;
  align-items: center;
  padding: 15px;
  font-size: 15px;
  color: #828282;
  border-bottom: 1px solid #E9E9E9;
}
.check-info .check-sex span{
  margin: 0 6px;
  padding: 6px 25px;
  background: #fff;
  border: 1px solid #D7D7D7;
  border-radius: 4px;
}
.check-info .check-sex span.on{
  background: #0977C5;
  border: 1px solid #0977C5;
  color: #fff;
}
.booking>ul>li{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #E9E9E9;
}
.booking-room{
  width: 50%;
  line-height: 24px;
  font-size: 17px;
  color: #1E1E1E;
}
.booking-room span{
  display: block;
  font-size: 15px;
  color: #828282;
}
.booking-price{
  width: 26%;
  font-size: 14px;
  text-align: right;
}
.booking-price span{
  font-size: 18px;
}
.booking>ul>li> .mint-button{
  padding: 0 15px;
  border: 1px solid #0977C5;
  color: #0977C5;
}




