
.page-jobs{
  padding-bottom:50px;
}
.post-info{
  padding:20px 15px;
  background:#fff;
}
.post-info-foot{
  margin-top:15px;
}
.post-info-foot span{
  display: inline-block;
  margin-right:12px;
  font-size: 14px;
  padding: 0 12px;
  height: 33px;
  line-height:33px;
  border: 1px solid #D7D7D7;
  border-radius:4px;
  background:#fff;
  color: #828282;
}
.box-loc{
  border-bottom:none;
}
.mint-swipe {
  width: 100%;
  height: 160px;
  overflow: hidden;
}
.mint-swipe-item img{
  width: 100%;
  height: 100%;
}
.fix-post{
  display:flex;
}
.fix-post .mint-button--normal{
  flex: 1;
  background:#fff;
  font-size:14px;
  box-shadow:none;
  font-size:13px;
}
.fix-post .mint-button:first-child{
  border-right:1px solid #E9E9E9;
}
.fix-post .mint-button--normal label{
  display:block;
}
.fix-post .mint-button--primary{
  flex: 2;
  background: #0977C5;
  box-shadow:none;
  font-size:16px;
}



