
.common-title{
  height: 44px;
  padding:0 10px;
  font-size: 16px;
  display: block;
  border-bottom:1px solid #E9E9E9;
}
.common-title-text{
  display: inline-block;
  vertical-align: middle;
  max-width: 85%;
  height: 40px;
  line-height: 46px;
  color:#1E1E1E;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}




