
.page-apply{
    background: #f7f7f7;
}
.apply-city{
    margin-bottom:20px;
    padding:15px;
    border-bottom: 1px solid #E9E9E9;
}
.apply-city h3{
    height: 30px;
    font-size: 15px;
    font-weight: 400;
    color:#f00;
}
.apply-city p{
    margin-bottom: 6px;
    font-size:15px;
}
.apply-group{
    margin: 10px 0 20px;
}
.apply-group .group-hd{
    padding: 0 15px;
    font-size: 15px;
    font-weight: 400;
    color: #828282;
}
.apply-group .group-hd span{
    font-size:12px;
    color: #999;
}
.apply-group .group-bd{
    margin: 10px 10px;
}
.apply-group .group-bd p{
    padding: 5px 10px;
}
.apply-mint .mint-cell-wrapper{
    background: none;
    border: 1px solid #C3C9D0;
    border-radius: 2px;
}
.sex-mint{
    border: 1px solid #C3C9D0;
    border-radius: 2px;
}
.sex-mint .mint-radiolist-title{
    display:none;
}
.sex-mint .mint-cell .mint-cell-wrapper{
    background: none;
    border-bottom: 1px solid #C3C9D0;
}
.sex-mint .mint-cell:last-child{
    background: #fff;
}
.sex-mint .mint-cell:last-child .mint-cell-wrapper{
    border:none;
}
.select-box{
    position: relative;
    height: 50px;
    margin-bottom:10px;
    border: 1px solid #C3C9D0;
    border-radius: 2px;
    background: #fff;
}
.dropdown{
    padding-right: 24px;
    vertical-align: top;
    width: 99%;
    height: 48px;
    position: absolute;
    top: 1;
    left: 1;
    bottom: 0;
    z-index: 3;
    font-size:16px;
    -webkit-appearance: none;
    color:#333;
    padding-left:15px;
    background: none;
    border: 0;
}
.select-box .dropdown-trigger {
    position: absolute;
    top: 22px;
    right: 15px;
    margin-top: -2px;
    display: inline-block;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-top: 8px solid #555;
    border-bottom-width: 0;
    z-index: 2;
}
.apply-group .mint-field .mint-cell-title{
    width: 0;
    padding-left: 45px;
}
.apply-group .mint-field .mint-field-core{
    height:40px;
    color:#333;
}
.process-handle{
    border-top: 1px solid #E9E9E9;
    padding:30px 10px;
}
.process-handle button{
    background: #0977C5;
}
  



