
.order-item{
  margin:8px 0;
  padding:15px 15px 0;
  background: #FFFFFF;
  border: 1px solid #E9E9E9;
}
.order-item-bd{
  margin-bottom:10px;
  font-size:16px;
}
.order-item-bd li label{
  width:50px;
  color:#1E1E1E;
}
.order-item-ft{
  display:flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-top: 1px dashed #D7D7D7;
}
.order-item-ft .money{
  font-size: 17px;
}
.order-item-ft .btn-link{
  padding:5px 20px;
  font-size:15px;
  color:#F9621D;
  background: #FFFFFF;
  border: 1px solid #F9621D;
  border-radius: 4px;
}



