
.shop-item{
  margin:10px 0;
  padding:20px 15px;
  background: #fff;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
}
.shop-item .item-foot{
  margin:20px 0 0 0;
  padding: 15px 0 0 0;
  border-top: 1px dashed #E9E9E9;
  display:flex;
}
.shop-item .item-foot li{
  margin-right:20px;
  font-size:15px;
  color:#828282;
}
.shop-item .item-foot li span{
  margin:0 3px;
  vertical-align: middle;
}





