html,body,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,form,input,label,textarea,p {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none
}
html, body {
  -webkit-overflow-scrolling: touch;
  user-select: none;
}
body{
  font-family: Arial,sans-serif;
  color: #5e5e5e;
  background:#f0f0f0;
  overflow-x: hidden;
}

img {
  border: 0;
  vertical-align: middle
}

ol,ul {
  list-style: none
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

a {
  text-decoration: none
}


.hide {
  display: none!important;
  visibility: hidden!important
}

.show {
  display: block!important
}


.orange{
  color: #F9621D!important;
}
.gray{
  color: #B4B4B4!important;
}
.tl{
  text-align: left!important;
}
.mint-button--default{
  background: #fff;
}


.mint-header{
  height: 44px;
  background: #0977C5;
  font-size: 17px;
}
.mint-header .mintui{
  font-size: 18px;
  font-weight: bold;
}
.mint-header .mint-header-title{
  height: 24px;
  line-height: 26px;
}

.fix-bottom{
  z-index: 2000;
  width: 100%;
  left: auto;
  right: auto;
  bottom: 0;
  position: fixed;
  background: #fff;
  box-shadow: 0 -1px 2px 0 rgba(0,0,0,0.12);
}
.fix-bottom .mint-button{
  height: 50px;
  font-size: 16px;
  border-radius: 0;
}
.fix-bottom .mint-button--large{
  background: #0977C5;
  box-shadow: 0 -1px 2px 0 rgba(0,0,0,0.12);
}


/**/
.list-info{
  margin:0;
  padding:0;
}
.list-info li{
  display: flex;
  padding: 5px 0;
}

.list-info li label{
  width:76px;
  height:20px;
  text-align:right;
  color:#828282;
}

.list-info li span {
  flex: 1;
  margin-left:5px;
  color:#1E1E1E;
}


.list-claim li{
  background-image: linear-gradient(0,#d9d9d9,#d9d9d9 50%,transparent 0);
  background-size: 120% 1px;
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-origin: content-box;
  padding:0;
  height:50px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.list-claim li label{
  width:120px!important;
  padding-left:10px;
  margin:15px 0;
}
.list-claim li label i{
  margin-top:-8px;
  margin-right:10px;
}
.list-claim li span{
  margin:15px 0;
}

/**/
.box{
  display:block;
  margin-bottom:10px;
  background:#fff;
  border-top:1px solid #E9E9E9;
  border-bottom:1px solid #E9E9E9;
}
.box-desc{
  margin:0;
  padding:10px 15px;
  font-size:15px;
  line-height:20px;
  color: #828282;
}
.box-desc p{
  margin:0;
  padding:4px 0;
}
.box-pic{
  padding:10px;
}

/**/
.mix-box{
  display: flex;
}
.mix-box .mix-pic{
  width:60px;
  height:60px;
  border-radius: 30px;
  background:#f0f0f0;
}
.mix-box .mix-info{
  flex: 1;
  margin-left:10px;
  color: #1E1E1E;
}
.mix-box .mix-title{
  margin:10px 0;
  font-size: 17px;
  line-height: 20px;
}
.mix-box .mix-desc{
  margin:0;
  font-size: 15px;
  line-height: 16px;
}

/**/
.check-date{
  display: flex;
  height: 83px;
}
.check-date >div{
  flex:1;
  font-size: 13px;
  color: #828282;
  padding: 12px 15px;
  line-height: 26px;
}
.check-date >.check-in{
  padding-right: 0;
}
.check-date >.check-out{
  padding-left: 0;
}
.check-date >div>span{
  clear: both;
  display: block;
  font-size: 20px;
  color: #1E1E1E;
}
.check-date >div.check-day{
  padding:12px 0;
  text-align:center;
  line-height: 65px;;
  font-size:16px;
  background:url(../../images/check_day_icon@3x.png) 32% 15px no-repeat;
  background-size: 57px auto;
}
.check-date >div.check-out{
  text-align:right;
}

/*hotel*/
.hotel-name{
  display: flex;
  align-items:center;
  margin: 5px 0 10px;
  font-size:17px;
  color: #1E1E1E;
}
.hotel-extra-info{
  margin:5px 0;
  max-width: 80%;
  font-size: 15px;
  color: #828282;
}
.hotel-tag{
  margin-left:8px;
  padding:0 5px;
  height:16px;
  font-size:12px;
  font-weight:400;
  color:#0977C5;
  background: #FFFFFF;
  border: 1px solid #0977C5;
  border-radius: 2px;
}

/*msgbox*/
.mint-msgbox{
  width: 80%;
  border-radius:8px;
}
