.mint-msgbox-header{
  width: 100%;
  height: 80px;
  padding:0;
  line-height:80px;
  background:url(../../images/mine_bg@3x.jpg) 0 0 no-repeat;
  background-size: 100% 80px;
}
.mint-msgbox-title{
  font-size:20px;
  color:#fff;
  font-weight: 400;
}
.mint-msgbox-content{
  border-bottom: 1px solid #E9E9E9;
}
.mint-msgbox-message{
  line-height:26px;
  font-size: 16px;
  color: #1E1E1E;
}
.mint-msgbox-message span{
  color: #0977C5;
}

.mint-msgbox-btns{
  height:50px;
  line-height:50px;
}
.mint-msgbox-btn{
  font-size: 16px;
}
.mint-msgbox-cancel{
  border-right: 1px solid #E9E9E9;
}
.mint-msgbox-confirm{
  color: #0977C5;
}

/*infinite*/
.list-infinite-wrapper {
  overflow: scroll;
}

.list-infinite-loading {
  text-align: center;
  height: 50px;
  line-height: 50px;
}
.list-infinite-loading div {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}
.no-more{
  padding:10px 0;
  color:#888;
  font-size:13px;
  text-align:center;
}

